<template>
  <div>
        <van-nav-bar title="报修处理（外修）" />
        <van-form @submit="onSubmit">
            <van-field v-model="fid" name="fid" style="display:none"/>
            <van-cell title="楼号:" :value="frepair_build"  />
            <van-cell title="区域:" :value="area"  />
            <van-cell title="报修类型:" :value="frepair_type"  />
            <van-cell title="故障描述:" :value="frservice_describe" />
            <van-button type="info" plain  block @click="getimg()"  style="width:90%;margin:10px auto">查看维修图片</van-button>
            <van-field v-model="repair_money" name="repair_money" label="金额:" placeholder="请输入金额"/>
            <van-field
                name="remark"
                v-model="remark"
                rows="2"
                autosize
                label="备注"
                type="textarea"
                maxlength="30"
                placeholder="请输入"
                show-word-limit
            />
            <van-button  block type="info" native-type="submit" style="width:90%;margin:10px auto">提交</van-button>
        </van-form>  
  </div>
</template>

<script>
import { ImagePreview } from 'vant';
import {apioutsidesave} from "@/api/index"
import {apioutsideinfo} from "@/api/index"
import { Toast } from 'vant';
export default {
    name:'outsideRepair',
    data(){
        return{
            fid:"",
            frepair_type:"",
            frepair_build:"",
            area:"",
            frservice_describe:"",
            img:[],
            repair_money:"",
            remark:'',
        }
    },
    methods:{
        // 图片预览
        getimg(){
            ImagePreview(this.img);
        },
        // 待维修信息渲染
        getrepairinfo(){
            var _this=this
            let params = {fid: this.fid}
            apioutsideinfo(params).then(res=>{
                console.log(res.data)
                var data=res.data.data[0]
                _this.frepair_type=data.frepair_type
                _this.frepair_build=data.frepair_build
                _this.frservice_describe=data.frservice_describe
                _this.area=data.area
                _this.img=data.url_list_array
            })
        },
        // 提交
        onSubmit(values) {
             this.$loading('提交中');
            apioutsidesave(values).then(res=>{
                if(res.data.code==200){
                      this.$loading.close();
                    Toast.success(res.data.msg);
                    setTimeout(() =>{
                       this.$router.push({
                            path:"/waitrepairlist"
                        });
                    },3000);
                }else{
                        this.$loading.close();
                        Toast.fail(res.data.msg);
                }     
                
            })
        }
    },
    created(){
        this.fid=this.$route.query.fid
        console.log(this.fid)
        this.getrepairinfo()
    }
}
</script>

<style>
.van-cell__value{
    flex: 2;
}
</style>


